@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

body {
  font-family: "Poppins", sans-serif;
  font-size: 12px;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
  list-style: none;
}

textarea {
  resize: none;
  width: 90%;
  font-family: "Poppins", sans-serif;
  font-size: 11px;
  border: none;
  padding: 3px 5px;
  margin: 5px;
  border-radius: 5px;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

textarea::-webkit-scrollbar {
  display: none;
}

input {
  width: 100px;
  margin: 5px;
  font-family: "Poppins", sans-serif;
  font-size: 11px;
  border: none;
  padding: 3px 5px;
  border-radius: 5px;
}

.input-120 {
  width: 120px;
}

button {
  outline: none;
  color: white;
  background-color: #5d83c0;
  border: none;
  width: fit-content;
  padding: 0px 10px;
  height: 25px;
  font-family: "Poppins", sans-serif;
  border-radius: 5px;
  font-size: 11px;
}

.input-200 {
  width: 200px;
}

.header-2 {
  font-size: 1.4em;
  color: #5d83c0;
  font-weight: 700;
  margin-bottom: 0.05in;
}

.split {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.gap {
  display: flex;
  align-items: center;
  gap: 50px;
}
